      {"name":"recall_number","type":"string","desc":"A numerical designation assigned by FDA to a specific recall event used for tracking purposes."},
      {"name":"event_id","type":"string","desc":"A numerical designation assigned by FDA to a specific recall event used for tracking purposes."},
      {"name":"product_type","type":"string","desc":"The type of product being recalled. For drug queries, this will always be Drugs."}
